
import { defineComponent, ref } from 'vue'

export default defineComponent({
  props: {
    id: {
      type: Number,
      default: undefined
    },
    isDone: {
      type: Boolean,
      default: false
    },
    content: {
      type: String,
      default: ''
    },
    showControlButton: {
      type: Boolean,
      default: true
    }
  },
  emits: ['update:isDone', 'update:content', 'delete'],
  setup () {
    const editValue = ref('')
    const isEdit = ref(false)

    return { editValue, isEdit }
  }
})
